export default {
	data() {
		return {
			info: {
				name: '沐枫',	//姓名
				sex: 1,			//姓别
				votes: '8',		//水滴值
			},
			pluss: false,		//+1动画
			mupiao: 1,			//成长进度 小1，中2 ，大3
			movetree: true,		//树动画开关
			treemove: false,	//树大小类型
			wateroff: true, 	//浇水动画开关
			watercss: false,	//水壶动画开关
			waterdom: false		//水滴动画开关
		};
	},
	
	methods: {
		//点击头像放大
		zoom(o) {
			uni.previewImage({
				'urls': [o]
			});	
		},
		
		//点击树的动画
		tree() {
			if(this.movetree) {
				this.treemove = true;
				this.movetree = false;
				setTimeout(() => {
					this.movetree = true;
					this.treemove = false;
				}, 1000);
			};
		},
		
		//点击浇水动画
		water() {
			if(this.wateroff) {
				this.watercss = true;
				this.wateroff = false;
									
				setTimeout(() => {
					this.waterdom = true;
				}, 1500);
				
				setTimeout(() => {
					this.info.votes++;
					this.pluss = true;
					this.treemove = true;
					this.movetree = false;
				}, 2000);
				
				setTimeout(() => {
					this.waterdom = false;
					this.movetree = true;
				}, 3500);
				
				setTimeout(() => {
					this.pluss = false;
					this.wateroff = true;
					this.watercss = false;
					this.treemove = false;
					if(10 <= this.info.votes && 20 > this.info.votes) {
						this.mupiao = 2;
					};
					if(20 <= this.info.votes ) {
						this.mupiao = 3;
					};
				}, 4000);	
			};
		}
	}
};
